<template>
  <v-footer
    padless
    :fixed="logged"
    color="black"
    class="white--text"
    :app="logged"
  >
    <v-col class="margin" v-if="$route.path.toLowerCase() == '/index'">
      <v-row cols="12" dark class="text-body-2 text-sm-h6 text-md-h6 text-lg-h6">
        <v-col class="col-12 col-sm-6 col-md-6 col-lg-6 my-16">
            <span class="mb-5">Contact</span>
            <br>
            <a href = "mailto: krishgokul351@gmail.com"><v-icon dark>mdi-email</v-icon>&nbsp;&nbsp;krishgokul351@gmail.com</a>
            <br>
            <a href="tel:+91 7339556554"><v-icon dark>mdi-phone</v-icon>  +91 7339556554</a>
            <br>
            <img v-show="$vuetify.breakpoint.smAndUp" width="70%" src="../assets/calling.svg" alt="">
        </v-col>
        <v-col class="col-12 col-sm-6 col-md-6 col-lg-6">
          <v-form :class="{'ma-16': $vuetify.breakpoint.mdAndUp}">
            <div class="mb-1">Report Bug / Issue</div>
            <br>
            <label for="name">Email</label>
            <v-text-field
              solo
              name="name"
              type="text"
            ></v-text-field>
            <label for="note">Description</label>
            <v-textarea
              solo
              name="note"
            ></v-textarea>
            <v-btn dark class="float-right">Report</v-btn>
          </v-form>
        </v-col>
      </v-row>
    </v-col>
    <v-col
      class="text-center z-pad"
      cols="12"
      v-else
    >
      &copy; {{ new Date().getFullYear() }} - OnePass
    </v-col>
  </v-footer>
</template>

<script>
export default {
  data: () => ({
    icons: [
      'mdi-facebook',
      'mdi-twitter',
      'mdi-linkedin',
      'mdi-instagram'
    ]
  }),
  computed: {
    logged () {
      return this.$store.state.logged
    }
  }
}
</script>

<style scoped>
  .z-pad {
    padding: 0 0 0 0;
  }
  .v-application {
    line-height: 0.2;
  }
  .margin {
    margin: 1% 5% 0% 3%;
  }
</style>



