<template>
  <v-container
    :class="$vuetify.breakpoint.mdAndUp ? 'fill-height' : 'py-0'"
    fluid
  >
    <!-- <link href="https://cdn.jsdelivr.net/npm/@mdi/font@latest/css/materialdesignicons.min.css" rel="stylesheet"> -->
    <v-row
      align="center"
      justify="center"
    >
      <v-col
        cols="12"
        sm="6"
        md="6"
        lg="4"
        align="center"
      >
        <v-alert
          :value="success"
          dense
          outlined
          text
          type="success"
          center
          style="width: fit-content"
        >
          Registration Success
        </v-alert>
        <v-card class="pb-3" :class="[$vuetify.breakpoint.smAndDown ? 'elevation-0' : 'elevation-12']">
          <v-card-title
            class="justify-center"
            :class="{'pt-0' : $vuetify.breakpoint.smAndDown}"
          >
            REGISTER
            <!-- <LogoText/> -->
          </v-card-title>
          <v-progress-linear
            :active="loading"
            indeterminate
            color="primary"
          ></v-progress-linear>
          <!-- <v-toolbar
            flat
          >
            <v-toolbar-title>
              CREATE ACCOUNT
            </v-toolbar-title>
            <v-spacer/>
            OR&nbsp;&nbsp;
            <v-toolbar-title>
              <router-link to="Login">
                LOGIN
              </router-link>
            </v-toolbar-title>
          </v-toolbar>

          <v-divider></v-divider> -->
          <v-card-text :class="[$vuetify.breakpoint.smAndDown ? 'px-2' : 'px-10']">
            <v-form :disabled="loading">
              <v-text-field
                label="Email address"
                name="email"
                type="text"
                hide-details: auto
                v-model="email"
                class="pt-0"
              ></v-text-field>
              <v-text-field
                label="Name"
                name="name"
                type="text"
                v-model="name"
              ></v-text-field>
              <v-text-field
                label="Master Password"
                name="masterpassword"
                type="password"
                v-model="masterpassword"
              ></v-text-field>
              <v-text-field
                label="Confirm Master Password"
                name="confirmmpwd"
                type="password"
                v-model="confirmmpwd"
              ></v-text-field>
              <v-text-field
                label="Remainder"
                name="remainder"
                type="text"
                v-model="remainder"
              ></v-text-field>

              <v-btn
                block
                rounded
                large
                color="primary"
                @click="register"
                :disabled="loading"
              >
                Create Account
              </v-btn>
            </v-form>
          </v-card-text>
          Already have an account ? <router-link to="Login">Login</router-link>
        </v-card>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AuthenticationServices from '@/services/AuthenticationService'
import encrypt from '@/services/encryption'
import LogoText from '@/components/LogoText'
export default {
  name: 'Register',
  data () {
    return {
      email: '',
      name: '',
      masterpassword: '',
      confirmmpwd: '',
      remainder: '',
      success: false,
      error: null,
      loading: false
    }
  },
  methods: {
    async register () {
      this.loading = true
      try {
        var hashData = await encrypt.hashPassword(this.email, this.masterpassword)
        var hashConfirmData = await encrypt.hashPassword(this.email, this.confirmmpwd)
        await AuthenticationServices.register({
          email: this.email,
          name: this.name,
          masterpassword: hashData,
          confirmmpwd: hashConfirmData,
          remainder: this.remainder
        })
        this.success = true
        this.$refs.form.reset()
        this.loading = false
      } catch (error) {
        this.loading = false
        const errors = error.response.data.error
        var errorMessages = ''
        errors.forEach(error => {
          if (errorMessages !== '') {
            errorMessages += '\n'
          }
          errorMessages += error.msg
        })
        this.error = errorMessages
      }
    }
  },
  components: {
    LogoText
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
@media screen and (max-width: 960px) {
  .v-input {
    padding-top: 0;
  }
}
</style>



